  mounted() {
    // temporary solution: only allowing my IPs to access webpify
    axios.get('https://api.ipify.org?format=json').then(res => {
      if (process.env.VUE_APP_WebpifyIPs.split(",").indexOf(res.data.ip) > -1) {
